.TodoTemplate {
    width: 512px;
    margin: 2rem auto 0;
    border-radius: 10px;
    overflow: hidden;
    background-color: #fffce8;

    .app-title {
        display: flex;
        justify-content: center;
        align-items: center;
        color: #545845;
        height: 87px;
        font-weight: bold;
        font-size: 1.667rem;
        background-color: #ccd5ae;
    }
    
    .content {
        display: flex;
        flex-direction: column;
        align-items: center;
        overflow-y: auto;
        width: 470px;
        min-height: 600px;
        margin: 24px 20px;
        border-radius: 10px;
        background-color: #fffadd;
    
        &::-webkit-scrollbar {
            display: none;
        }
    }
}

